export const countStarting = <T>(array: T[], isTarget: (value: T) => boolean) => {
    let isCounting = false;

    let counter = 0;
    for (const value of array) {
        if(!isCounting) {
            isCounting = isTarget(value);
        }

        if(!isCounting) continue;

        counter++;
    }

    return counter;
}
